import React, { Component } from 'react';
import { Tabs, message } from 'antd';
import { browserHistory } from 'react-router';
import Scrollbars from 'react-custom-scrollbars';
import classnames from 'classnames';
import CustomerArchiveBlock from './customer-archive-block';
import WechatPay from 'components/wechat-pay-popup';
import CashPopup from 'components/cash-popup';
import PromptPopup from 'components/prompt-popup';


import '../modal.css';


import './booking_pay.less';
import { inject, observer } from 'mobx-react';
const TabPane = Tabs.TabPane;

@inject('bookingStore') @observer
class PayBooking extends Component {

	constructor(props, context) {
		super(props, context);
		this.state={receiptPopup:'',payPopup:''};
		
		let bookingStore = this.props.bookingStore;

		// 
		if(this.props.params.bookingID){

			let payBookingID = this.props.params.bookingID;
			
			bookingStore.payBookingID= payBookingID;

			bookingStore.getBookingPaymentInfo(payBookingID);

		}else{
			//
			bookingStore.paySaveBooking(this.props.location.state.cart.shoppingCart);	
		}
		
		
	}

	componentDidMount() {
		let bookingStore = this.props.bookingStore;

		bookingStore.getPaymentMethod();//
	}

//bookingStore.payBookingID,method,payMoney
	payClick=(payBookingID,method,payMoney)=>{
		
			switch(method.paymentMethodID){
				case 1://
					this.setState({payPopup:<WechatPay title={""} onCancel={()=>{
						this.setState({payPopup:""});
					}}  onOk={(code)=>{
						
						this.props.bookingStore.payment(payBookingID,method,payMoney,code);
					}} inputValue={payMoney}/>});					
				break;
				case 2://
					this.setState({payPopup:<WechatPay title={""} onCancel={()=>{
						this.setState({payPopup:""});
					}} onOk={(code)=>{
						
						this.props.bookingStore.payment(payBookingID,method,payMoney,code);
					}} inputValue={payMoney}/>});
				break;
				case 3:	//
					// this.setState({payPopup:<CashPopup title={""} onCancel={()=>{
					// 	this.setState({payPopup:""});
					// }} onOk={(money)=>{
						
					// }}/>});
				break;
				case 4://k
					// this.setState({payPopup:<CashPopup title={"k"} onCancel={()=>{
					// 	this.setState({payPopup:""});
					// }} onOk={(money)=>{

					// }}/>});
				break;
				case 5://
					
					 this.setState({payPopup:<PromptPopup onCancel={()=>{
						this.setState({payPopup:""});
					 }} onOk={()=>{
						
						this.props.bookingStore.payment(payBookingID,method,payMoney,'');
						this.setState({payPopup:""});
					 }}>
					 	<div className="prompt">
						<div className="delele-text">
						<span>{payMoney}</span>
						</div>
					</div>
					 
					</PromptPopup>});
				break;
			}
	}

	render() {
		let bookingStore = this.props.bookingStore;
		let paymentInfo = bookingStore.paymentInfo;
		let bookingTypeName,bookingStatusName;
		let totalMoney;//
		let hasPayMoney;//
		
	
		if(paymentInfo){
			hasPayMoney = paymentInfo.bookingDetail.paidAmount;
			hasPayMoney= hasPayMoney?hasPayMoney:0;

		}
		
		switch(paymentInfo&&paymentInfo.bookingDetail.bookingType){
			case 614:
				bookingTypeName="";
				totalMoney=paymentInfo.bookingDetail.actualAmount;
			break;
			case 615:
				bookingTypeName="";
				totalMoney= paymentInfo.bookingDetail.bookingAmount;
			break;
			case 616:
				bookingTypeName="";
			break;

			default:		
			break;
		}
		switch(paymentInfo&&paymentInfo.bookingDetail.bookingStatus){
				case 617:
					bookingStatusName="";
				break;                                   
				case 618:
					bookingStatusName="";
				break;
				case 619:
					bookingStatusName="";
				break;
				case 620:
					bookingStatusName="";
				break;
				case 621:
					bookingStatusName="";
				break;
				case 622:
					bookingStatusName="";
				break;
				case 729:
					bookingStatusName="";
				break;
				case 759:
					bookingStatusName="";
				break;
				case 762:
					bookingStatusName="";
				break;
				case 1002:
					bookingStatusName="";
				break;
				case 1012:
					bookingStatusName="";
				break;
			}
		
		
    		return <div id="pay_container">
			
				<div className="pay-title"><i className="iconfont icon-order_btn_back" onClick={()=>{
					
					bookingStore.payGoBack();
					//
				}}></i></div>
				{<div className="pay-container">
					<div className="pay-info-block">
						<div className="info-content">
							<div className="pay-phone">
								<i className="iconfont icon-huiyuanchongzhi_icon_wechat"></i>
								<div className="contact-person">
									<p className="name">{paymentInfo&&paymentInfo.bookingDetail.contact}</p>
									<p className="phone">{paymentInfo&&paymentInfo.bookingDetail.mobile}</p>
								</div>
							</div>
							<div className="pay-left-content">
								<div className="order-num">{paymentInfo&&paymentInfo.bookingDetail.bookingCode}</div>
								<ul className="booking-time-ul">
									<li>
										<span className="left-name">:</span>
										<span className="right-content">{paymentInfo&&paymentInfo.bookingDetail.bookingTime}</span>
									</li>
									<li>
										<span className="left-name">:</span>
										<span className="right-content">{bookingStatusName}</span>
									</li>
									<li>
										<span className="left-name">:</span>
										<span className="right-content">{paymentInfo&&paymentInfo.bookingDetail.peopleNum}</span>
									</li>
									<li>
										<span className="left-name">:</span>
										<span className="right-content">{bookingTypeName}</span>
									</li>
									<li className="remark">
										<span className="left-name">:</span>
										<span className="right-content">{paymentInfo&&paymentInfo.bookingDetail.tableCodes}</span>
									</li>
									
									<li className="remark">
										<span className="left-name">:</span>
										<span className="right-content">{paymentInfo&&paymentInfo.bookingDetail.memo}</span>
									</li>
								</ul>
								
							</div>
							{paymentInfo&&paymentInfo.bookingDetail.bookingType==614&&<div className="dishes-list">								
								<Tabs defaultActiveKey="0">
								
								{paymentInfo&&paymentInfo.bookingDetail.bookingType==614&&paymentInfo.tableOrderDetail.map((table,index)=>{
									let item=table.orderDetail.map((ele,mindex)=>{
										return <div key={mindex} className="item">
											<span>{mindex+1}</span>
											<span>{ele.productName}</span>
											<span>{ele.quantity}</span>
											<span>{ele.price}</span>
											<span>{ele.valueNames}</span>
										</div>
									});
									return <TabPane tab={table.tableName} key={index}>
												<div className="my-list">
													<div className="list-title">
														<span></span>
														<span></span>
														<span></span>
														<span></span>
														<span></span>
													</div>
													<div className="list-content">
														<Scrollbars>
															{item}															
														</Scrollbars>										
													</div>
												</div>
										</TabPane>
								}) }
									
								</Tabs>
							</div>}
						</div>						
					</div>

					<div className="pay-info-rihgt">
						<div className="return-account">
							<div className="account-item">
								<span className="num">{totalMoney}</span>
								<span className="text"></span>
							</div>
							<div className="account-item">
								<span className="num">{hasPayMoney}</span>
								<span className="text"></span>
							</div>
							<div className="account-item">
								<span className="num">{totalMoney-hasPayMoney}</span>
								<span className="text"></span>
							</div>
						</div>
						<div className="payment-method"></div>
						{false&&<ul className="payment-method">
							<li>
								<span className="num">1</span>
								<span className="method"></span>
								<span className="account">20.00</span>
								<span className='ok'></span>
								<span className='cancel'></span>
							</li>	
							<li>
								<span className="num">2</span>
								<span className="method"></span>
								<span className="account">20.00</span>
								<span className='ok'></span>
								<span className='cancel'></span>
							</li>
							
							<li className="cash">
								<span className="num">3</span>
								<span className="method"></span>
								<span className="account">20.00</span>
								<i className="iconfont icon-shanchu1"></i>
							</li>
						</ul>}
						<ul className="payment">
							
							{bookingStore.paymentMethod&&bookingStore.paymentMethod.map((method,index)=>{
								return <li key={index}  onClick={()=>{
									let payMoney = totalMoney-hasPayMoney;
									if(payMoney>0){
										let bookingStore = this.props.bookingStore;
										this.payClick(bookingStore.payBookingID,method,payMoney);
									}									
								}}>
									<i className={classnames({
										'iconfont': true,
										'icon-account_btn_vip': method.paymentName === '',
										'icon-account_btn_cash': method.paymentName === '',
										'icon-account_btn_guazhang': method.paymentName === '',
										'icon-account_btn_wechat': method.paymentName === '',
										'icon-account_btn_alipay': method.paymentName === '',												
										'icon-account_btn_k': method.paymentName === 'K'												
									})}></i>
									<p>{method.paymentName}</p>
								</li>
							})}														
						</ul>
						<div className="info-rihgt-btns">
							<div onClick={()=>{
								browserHistory.push("/booking");
							}}></div>
							<div className="select" onClick={()=>{
								browserHistory.push("/booking");
							}}></div>
						</div>
					</div>		
				</div>}
				{this.state.payPopup}			
		   </div>
	}

}

PayBooking.wrappedComponent.contextTypes = {
	router: React.PropTypes.object.isRequired
}

export default PayBooking;




