import React, { Component } from 'react';
import {Modal} from 'antd';  
import './guest_search_popup.less';
import Scrollbars from 'react-custom-scrollbars';

class GuestSearchPopup extends Component {

	render() {
    return <div>
			<Modal  
                title=""
				visible={true} 
				maskClosable={false} 
				okText='' cancelText=''
				width={840}
				closable={false}
				wrapClassName="guest-search-popup-modal"    
				>      
				<div className="guest-search-container">
					<div className="search">
						<div className="calendar"></div>
						<input type="text" placeholder=""/>
						<div className="search-btn">
							<i className="iconfont icon-order_btn_search"></i>
							
						</div>
					</div>
					<div className="content">
						<div className="title">
							<span></span>
							<span></span>
							<span></span>
							<span></span>
							<span></span>
							<span></span>
							<span></span>
							<span></span>
						</div>
						<div className="items-block">
							<Scrollbars>
								<div className="item">
									<span>1</span>
									<span>04-16  19:30</span>
									<span></span>
									<span>13723458769</span>
									<span>4</span>
									<span>B02</span>
									<span></span>
									<span></span>
								</div>	
								<div className="item">
									<span>1</span>
									<span>04-16  19:30</span>
									<span></span>
									<span>13723458769</span>
									<span>4</span>
									<span>B02</span>
									<span></span>
									<span></span>
								</div>						
							</Scrollbars>
						
						</div>
						
					</div>
				</div>
			</Modal>
      </div>
	}

}

export default GuestSearchPopup;



