/*  */
import React from 'react';
import {Modal} from 'antd';
import { inject, observer } from 'mobx-react';
import { message, Button,Alert } from 'antd';

import './prompt_popup.less';

@inject('settlementStore') @observer
class PromptPopup extends React.Component {

  render() {
    let cancelText;
    letokText;
    if(this.props.cancelText){
      cancelText=this.props.cancelText; 
    }else{
       cancelText="";
    }

    if(this.props.okText){
      okText=this.props.okText; 
    }else{
       okText="";
    }
    return <Modal 
      title="" 
      visible={true} 
      maskClosable={false} 
      okText={okText} 
      cancelText={cancelText}
      width={510}
      wrapClassName="prompt-popup-modal"
      onCancel={()=>{
        this.props.onCancel && this.props.onCancel();
      }}
      onOk={()=>{
        this.props.onOk && this.props.onOk(); 
      }}
    >
      {this.props.children}
    </Modal>
  }
}

export default PromptPopup;


