/*  */
import React from 'react';
import { Modal } from 'antd';


import './wechat_pay_popup.less';


class WechatPay extends React.Component {
  constructor(props){
    super(props);
    this.state={inputValue:''}
  }
 

  render() {
    // 
    let cance=<button className="cancle" onClick={()=>{
              if(this.props.onCancel){
                this.props.onCancel();
              }
            }}></button>
    return (
      <div id="wechat-pay">
        <Modal title={this.props.title}
            visible={true} 
            maskClosable={false} 
            okText='' cancelText=''
            // footer={null}
            width={500}
            wrapClassName="wechat-pay-modal"
            onCancel={()=>{
              if(this.props.onCancel){
                this.props.onCancel();
              }
            }}
            onOk={()=>{
              if(this.props.onOk){
                this.props.onOk(this.state.inputValue);
              }
            }}
        >
          <div>
            <div className="change-tab"></div>
            <p className="wechat-text"><span className="wechat-amount">{this.props.inputValue}</span></p>
            <p className="wechat-text"><input type="text" value={this.state.inputValue} onChange={(e)=>{
                this.setState({inputValue:e.target.value})
            }}/></p>
            
          </div>
        </Modal>
      </div>
    )
  }
}

export default WechatPay;


