import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import {Modal} from 'antd';  
import moment from 'moment';

import './payment_receipt_popup.less';

@inject('bookingStore') @observer
class PayReceiptPopup extends Component {


	okClick=()=>{
		if(this.props.okClick){
			this.props.okClick();
		}
	}
	render() {
		let bookingStore = this.props.bookingStore;
    	return <div>
			<Modal  
				visible={true} 
				maskClosable={false} 
				footer={null}
				width={522}
				closable={false}
				wrapClassName="receipt-popup-modal"    
				>      
				<div className="receipt-container">
					<div className="receipt-title">{bookingStore.bookingSuccessInfo.merchantName}</div>
					<ul className="receipt-info">
						<li><span className="title"></span>{bookingStore.bookingSuccessInfo.bookingCode}</li>
						<li><span className="title"></span>{bookingStore.bookingSuccessInfo.phone}</li>
						<li><span className="title">   </span>{bookingStore.bookingSuccessInfo.contact}</li>
						<li><span className="title"></span>{moment(bookingStore.bookingSuccessInfo.bookingTime).format("MM-DD HH:mm")}</li>
						<li><span className="title">   </span>{bookingStore.bookingSuccessInfo.peopleNum}</li>
						<li><span className="title"></span>{bookingStore.bookingSuccessInfo.tableCodes}</li>
						<li><span className="title"></span>{bookingStore.bookingSuccessInfo.bookingType}</li>
						<li><span className="title">   </span>{bookingStore.bookingSuccessInfo.bookingAmount}</li>
					
					</ul>
					{false&&<div className="receipt-payment">
						<div className="payment">
							<span></span>
							<ul>
								<li></li>
								<li></li>
							</ul>
						</div>
						<div className="money">
							<span></span>
							<ul>
								<li>1680</li>
								<li>1000</li>
							</ul>
						</div>
					</div>}
					<div className="receipt-remarks">
						<div className="remarks">
							<span className="title"></span>
							{bookingStore.bookingSuccessInfo.memo}
						</div>
						<ul className="person">
							<li><span className="title"></span>{bookingStore.bookingSuccessInfo.customerName}</li>
							<li><span className="title"></span>{bookingStore.bookingSuccessInfo.createTime}</li>
						</ul>					
					</div>
					<div className="receipt-address">
						<div><span className="title"></span>{bookingStore.bookingSuccessInfo.managerPhone}</div>
						<div><span className="title"></span>{bookingStore.bookingSuccessInfo.postalAddress}</div>
					</div>
					<div className="receipt-notice">
						XX
					</div>

					<div className="confirm-btn" onClick={this.okClick}></div>
				</div>
			</Modal>
      </div>
	}

}

export default PayReceiptPopup;



